import React from 'react';
import {
    HashRouter,
    Switch,
    Route,
    Link, // TODO Remove unneeded Link import
} from "react-router-dom";
import styles from './App.module.css'; // TODO Remove unneeded line
import Home from './pages/home/home';
import Testimony from './pages/testimony/testimony';
import PagesCounter from './pages/counter/counter';
import Book from './pages/book/book';

function App() {
    return (
        <HashRouter basename="/">
            <Switch>
                <Route path="/counter">
                    <PagesCounter/>
                </Route>
                <Route path="/book">
                    <Book/>
                </Route>
                <Route path="/testimony">
                    <Testimony/>
                </Route>
                <Route path="/">
                    <Home/>
                </Route>
            </Switch>
        </HashRouter>
    );
}

export default App;
